:root {
  --background: #ffffff;
  --foreground: #2C2C2C;

  /* Palette algérienne */
  --green-algerian: #006233;
  --green-light: #1FA27A;
  --red-algerian: #D21034;
  --red-light: #F15B5B;
  --gray-dark: #2C2C2C;
  --gray-light: #E9ECEF;
  --white: #FFFFFF;
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: #FFFFFF;
    --foreground: #2C2C2C;
  }
}

body {
  color: var(--foreground);
  background: var(--background);
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  max-width: 1200px !important;
}

.container-narrow {
  max-width: 1000px !important;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.max-w-content {
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
@keyframes blob {
  0% { transform: translate(0px, 0px) scale(1); }
  33% { transform: translate(30px, -50px) scale(1.1); }
  66% { transform: translate(-20px, 20px) scale(0.9); }
  100% { transform: translate(0px, 0px) scale(1); }
}

.animate-blob {
  animation: blob 7s infinite;
}

